import { Component, Directive, ContentChildren, QueryList, AfterContentInit } from '@angular/core';

@Directive({selector: 'td-dialog-title'})
export class TdDialogTitleDirective {}

@Directive({selector: 'td-dialog-content'})
export class TdDialogContentDirective {}

@Directive({selector: 'td-dialog-actions'})
export class TdDialogActionsDirective {}

@Component({
  selector: 'td-dialog',
  template: `<div class="td-dialog-wrapper">
  <h3 class="td-dialog-title" *ngIf="dialogTitle.length > 0">
    <ng-content select="td-dialog-title"></ng-content>
  </h3>
  <div class="td-dialog-content" *ngIf="dialogContent.length > 0">
    <ng-content select="td-dialog-content"></ng-content>
  </div>
  <div class="td-dialog-actions" *ngIf="dialogActions.length > 0">
    <span class="td-dialog-spacer"></span>
    <ng-content select="td-dialog-actions"></ng-content>
  </div>
</div>`,
  styles: [`.td-dialog-title {
  margin-top: 0;
  margin-bottom: 20px; }

.td-dialog-content {
  margin-bottom: 16px; }

.td-dialog-actions {
  position: relative;
  top: 16px;
  left: 16px; }
  ::ng-deep [dir='rtl'] .td-dialog-actions {
    right: 16px;
    left: auto; }

:host {
  display: block; }
  :host .td-dialog-actions {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }
    :host .td-dialog-actions .td-dialog-spacer {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1; }
    :host .td-dialog-actions ::ng-deep button {
      text-transform: uppercase;
      margin-left: 8px;
      padding-left: 8px;
      padding-right: 8px;
      min-width: 64px; }
      [dir='rtl'] :host .td-dialog-actions ::ng-deep button {
        margin-right: 8px;
        margin-left: inherit; }
`],
})
export class TdDialogComponent implements AfterContentInit {

  @ContentChildren(TdDialogTitleDirective) dialogTitle: QueryList<TdDialogTitleDirective>;
  @ContentChildren(TdDialogContentDirective) dialogContent: QueryList<TdDialogContentDirective>;
  @ContentChildren(TdDialogActionsDirective) dialogActions: QueryList<TdDialogActionsDirective>;

  ngAfterContentInit(): void {
    if (this.dialogTitle.length > 1) {
      throw new Error('Duplicate td-dialog-title component at in td-dialog.');
    }
    if (this.dialogContent.length > 1) {
      throw new Error('Duplicate td-dialog-content component at in td-dialog.');
    }
    if (this.dialogActions.length > 1) {
      throw new Error('Duplicate td-dialog-actions component at in td-dialog.');
    }
  }

}
